import React, { useState } from 'react';
import { QrReader } from 'react-qr-reader';
import { Button, TextField, Typography, Container, Box,MenuItem, Select  } from '@mui/material';
import { useNavigate } from 'react-router-dom';

function ScanAndPay() {
  const [qrData, setQrData] = useState(null);
  const [amount, setAmount] = useState('');
  const [pin, setPin] = useState('');
  const [step, setStep] = useState(1);
  const [error, setError] = useState('');
  const [currency, setCurrency] = useState('USD');
  const [openSnackbar,setOpenSnackbar] = useState(false);
  const navigate = useNavigate();

  const handleScan = (result) => {
    if (result) {
      setQrData(result.text);
      setStep(2);
    }
  };

  const handleError = (err) => {
    setError('Error scanning QR code. Please try again.');
    console.error(err);
  };


  const handlePayment = () => {
    if (amount <= 0) {
      setError('Please enter a valid amount.');
      return;
    }
    if (pin !== '1234') {
      setError('Invalid PIN.');
      return;
    }

    if (MenuItem == 'USD'){
      alert(`Payment of $${amount} to ${qrData} successful.`);
    }
    else{
      alert(`Payment of €${amount} to ${qrData} successful.`);
    }
    
    
    navigate('/');
  };

  return (
    <Container>
      <Typography variant="h4" gutterBottom>Scan QR Code</Typography>
      {step === 1 && (
        <QrReader
          onResult={(result, error) => {
            if (result) {
              handleScan(result);
            }
            if (error) {
              handleError(error);
            }
          }}
          style={{ width: '100%' }}
          constraints={{ facingMode: 'environment' }} // Ensures the back camera is used
        />
      )}
      {step === 2 && (
        <Box sx={{ mt: 2 }}>
          <Typography variant="h6">Pay to: {qrData}</Typography>
          <TextField
            label="Enter amount"
            type="number"
            value={amount}
            onChange={(e) => setAmount(e.target.value)}
            fullWidth
            margin="normal"
          />
          <TextField
            label="Enter PIN"
            type="password"
            value={pin}
            onChange={(e) => setPin(e.target.value)}
            fullWidth
            margin="normal"
            
          />
          <Select
            fullWidth
            value={currency}
            onChange={(e) => setCurrency(e.target.value)}
            className="mb-4"
          >
            <MenuItem value="USD">USD</MenuItem>
            <MenuItem value="EUR">EUR</MenuItem>
  
          </Select>

          <Button variant="contained" color="primary" onClick={handlePayment} fullWidth sx={{ mt: 2 }}>
            Pay
          </Button>
         
          
        </Box>
        
      )}
    </Container>
    
  );
}

export default ScanAndPay;
