// Contact.js
import React from 'react';
import './Contact.css';

const Contact = () => {
  return (
    <div>
      <main className="flex-shrink-0">
        {/* <!-- Navigation--> */}
        <nav className="navbar navbar-expand-lg navbar-light bg-white py-3">
          <div className="container px-5">
            
            <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
              <span className="navbar-toggler-icon"></span>
            </button>
            <div className="collapse navbar-collapse" id="navbarSupportedContent">
              <ul className="navbar-nav ms-auto mb-2 mb-lg-0 small fw-bolder">
                <li className="nav-item"><a className="nav-link" href="/">Home</a></li>
                <li className="nav-item"><a className="nav-link" href="send-money">SendMoney</a></li>
                <li className="nav-item"><a className="nav-link" href="/scan">Scan</a></li>
                <li className="nav-item"><a className="nav-link" href="/transactions">TransactionHistory</a></li>
                <li className="nav-item"><a className="nav-link" href="//bank-accounts">Bank accounts</a></li>
              </ul>
            </div>
          </div>
        </nav>

        {/* <!-- Page content--> */}
        <section className="py-5">
          <div className="container px-5">
            {/* <!-- Contact form--> */}
            <div className="bg-light rounded-4 py-5 px-4 px-md-5">
              <div className="text-center mb-5">
                <div className="feature bg-primary bg-gradient-primary-to-secondary text-white rounded-3 mb-3">
                  <i className="bi bi-envelope"></i>
                </div>
                <h1 className="fw-bolder">Get in touch</h1>
                <p className="lead fw-normal text-muted mb-0">Let's work together!</p>
              </div>
              <div className="row gx-5 justify-content-center">
                <div className="col-lg-8 col-xl-6">
                  {/* <!-- Contact Form--> */}
                  <form id="contactForm" data-sb-form-api-token="API_TOKEN">
                    {/* <!-- Name input--> */}
                    <div className="form-floating mb-3">
                      <input className="form-control" id="name" type="text" placeholder="Enter your name..." data-sb-validations="required" />
                      <label htmlFor="name">Full name</label>
                      <div className="invalid-feedback" data-sb-feedback="name:required">A name is required.</div>
                    </div>
                    {/* <!-- Email address input--> */}
                    <div className="form-floating mb-3">
                      <input className="form-control" id="email" type="email" placeholder="name@example.com" data-sb-validations="required,email" />
                      <label htmlFor="email">Email address</label>
                      <div className="invalid-feedback" data-sb-feedback="email:required">An email is required.</div>
                      <div className="invalid-feedback" data-sb-feedback="email:email">Email is not valid.</div>
                    </div>
                    {/* <!-- Phone number input--> */}
                    <div className="form-floating mb-3">
                      <input className="form-control" id="phone" type="tel" placeholder="(123) 456-7890" data-sb-validations="required" />
                      <label htmlFor="phone">Phone number</label>
                      <div className="invalid-feedback" data-sb-feedback="phone:required">A phone number is required.</div>
                    </div>
                    {/* <!-- Message input--> */}
                    <div className="form-floating mb-3">
                      <textarea className="form-control" id="message" placeholder="Enter your message here..." style={{ height: '10rem' }} data-sb-validations="required"></textarea>
                      <label htmlFor="message">Message</label>
                      <div className="invalid-feedback" data-sb-feedback="message:required">A message is required.</div>
                    </div>
                    {/* <!-- Submit success message--> */}
                    <div className="d-none" id="submitSuccessMessage">
                      <div className="text-center mb-3">
                        <div className="fw-bolder">Form submission successful!</div>
                        To activate this form, sign up at
                        <br />
                        <a href="https://startbootstrap.com/solution/contact-forms">https://startbootstrap.com/solution/contact-forms</a>
                      </div>
                    </div>
                    {/* <!-- Submit error message--> */}
                    <div className="d-none" id="submitErrorMessage">
                      <div className="text-center text-danger mb-3">Error sending message!</div>
                    </div>
                    {/* <!-- Submit Button--> */}
                    <div className="d-grid">
                      <button className="btn btn-primary btn-lg disabled" id="submitButton" type="submit">Submit</button>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </section>
      </main>
      {/* <!-- Footer--> */}
      <footer className="bg-white py-4 mt-auto">
        <div className="container px-5">
          <div className="row align-items-center justify-content-between flex-column flex-sm-row">
            <div className="col-auto">
              <div className="small m-0">Copyright &copy; Your Website 2023</div>
            </div>
            <div className="col-auto">
              <a className="small" href="#!">Privacy</a>
              <span className="mx-1">&middot;</span>
              <a className="small" href="#!">Terms</a>
              <span className="mx-1">&middot;</span>
              <a className="small" href="#!">Contact</a>
            </div>
          </div>
        </div>
      </footer>
      {/* <!-- Bootstrap core JS--> */}
      <script src="https://cdn.jsdelivr.net/npm/bootstrap@5.2.3/dist/js/bootstrap.bundle.min.js"></script>
      {/* <!-- Core theme JS--> */}
      <script src="js/scripts.js"></script>
      {/* <!-- SB Forms JS--> */}
      <script src="https://cdn.startbootstrap.com/sb-forms-latest.js"></script>
    </div>
  );
}

export default Contact;
