import React, { useState } from 'react';
import { Box, Button, List, ListItem, ListItemText, TextField, Typography, Snackbar, Alert } from '@mui/material';

function ManageBankAccounts() {
  const [accounts, setAccounts] = useState([]);
  const [newAccount, setNewAccount] = useState('');
  const [openSnackbar, setOpenSnackbar] = useState(false);

  const handleAddAccount = () => {
    setAccounts([...accounts, newAccount]);
    setNewAccount('');
    setOpenSnackbar(true);
  };

  const handleCloseSnackbar = () => {
    setOpenSnackbar(false);
  };

  return (
    <Box className="p-4">
      <Typography variant="h5" align="center" className="mb-4">
        Manage Bank Accounts
      </Typography>
      <TextField
        fullWidth
        label="New Account"
        value={newAccount}
        onChange={(e) => setNewAccount(e.target.value)}
        className="mb-4"
      />
      <Button
        fullWidth
        variant="contained"
        color="primary"
        onClick={handleAddAccount}
        className="mb-4"
      >
        Add Account
      </Button>
      <List>
        {accounts.map((account, index) => (
          <ListItem key={index} className="mb-2">
            <ListItemText primary={account} />
          </ListItem>
        ))}
      </List>
      <Snackbar
        open={openSnackbar}
        autoHideDuration={6000}
        onClose={handleCloseSnackbar}
      >
        <Alert onClose={handleCloseSnackbar} severity="success">
          Bank account added successfully!
        </Alert>
      </Snackbar>
    </Box>
  );
}

export default ManageBankAccounts;
