import React from 'react';
import { Grid, Paper, Typography } from '@mui/material';
import { Link } from 'react-router-dom';

function Home() {
  const options = [
    // { label: 'Scan & Pay', path: '/scan' },
    { label: 'Send Money', path: '/send-money' },
    { label: 'Transaction History', path: '/transactions' },
    { label: 'Manage Bank Accounts', path: '/bank-accounts' },
    { label: 'Scan And Pay', path: '/scanandpay' },
  ];

  return (
    <Grid container spacing={2} style={{ padding: 20 }}>
      {options.map(option => (
        <Grid item xs={6} key={option.label}>
          <Link to={option.path} style={{ textDecoration: 'none' }}>
            <Paper elevation={3} style={{ padding: 20, textAlign: 'center', backgroundColor: '#f0f0f0' }}>
              <Typography variant="h6" style={{ color: '#333' }}>{option.label}</Typography>
            </Paper>
          </Link>
        </Grid>
      ))}
    </Grid>
  );
}

export default Home;
