import React from 'react';
import { BottomNavigation, BottomNavigationAction } from '@mui/material';
import { Link } from 'react-router-dom';
import HomeIcon from '@mui/icons-material/Home';
import ContactIcon from '@mui/icons-material/ContactPage';
import QrCodeScannerIcon from '@mui/icons-material/QrCodeScanner';
import AccountBalanceIcon from '@mui/icons-material/AccountBalance';
import HistoryIcon from '@mui/icons-material/History';
import SendIcon from '@mui/icons-material/Send';

function Navbar() {
  return (
    <BottomNavigation className="bg-primary fixed-bottom">
      <BottomNavigationAction label="Home" icon={<HomeIcon />} component={Link} to="/" />
      <BottomNavigationAction label="Contact" icon={<ContactIcon />} component={Link} to="/contact" />
      <BottomNavigationAction label="Scan And Pay" icon={<QrCodeScannerIcon />} component={Link} to="/scanandpay" />
      <BottomNavigationAction label="Send Money" icon={<SendIcon />} component={Link} to="/send-money" />
      <BottomNavigationAction label="Transactions" icon={<HistoryIcon />} component={Link} to="/transactions" />
      <BottomNavigationAction label="Bank Accounts" icon={<AccountBalanceIcon />} component={Link} to="/bank-accounts" />
    </BottomNavigation>
  );
}

export default Navbar;
