import React from 'react';
import { Box, List, ListItem, ListItemText, Typography } from '@mui/material';

const transactions = [
  { id: 1, recipient: 'Alice', amount: 50, date: '2024-06-01' },
  { id: 2, recipient: 'Bob', amount: 30, date: '2024-06-02' },
  { id: 3, recipient: 'Charlie', amount: 20, date: '2024-06-03' },
];

function TransactionHistory() {
  return (
    <Box style={{ padding: 20 }}>
      <Typography variant="h5" align="center" style={{ margin: '20px 0' }}>
        Transaction History
      </Typography>
      <List>
        {transactions.map((transaction) => (
          <ListItem key={transaction.id}>
            <ListItemText
              primary={`To: ${transaction.recipient} - Amount: $${transaction.amount}`}
              secondary={`Date: ${transaction.date}`}
            />
          </ListItem>
        ))}
      </List>
    </Box>
  );
}

export default TransactionHistory;
